function updateTimeSheet(context, table, data) {
  const row = table.rows.getItemAt(Number(data.rowIndex));
  let totalTime = calculateWorkTime(data.startTime, data.endTime);
  row.values = [[null, null, null, data.startTime, data.endTime, totalTime, data.desc]];
}

function insertTimeSheet(context, table, data) {
  let totalTime = calculateWorkTime(data.startTime, data.endTime);
  table.rows.add(null, [[data.date, data.taskID, data.pic, data.startTime, data.endTime, totalTime, data.desc]]);
}
export function calculateWorkTime(startTime, endTime) {
  // Kiểm tra nếu startTime và endTime đều không rỗng
  if (startTime && endTime) {
    // Chuyển đổi thời gian từ chuỗi sang đối tượng Date
    const start = new Date(`1970-01-01T${startTime}:00`);
    const end = new Date(`1970-01-01T${endTime}:00`);
    const lunchStart = new Date(`1970-01-01T12:00:00`);
    const lunchEnd = new Date(`1970-01-01T13:00:00`);

    // Tính toán thời gian làm việc
    let totalTime = (end - start) / (1000 * 60 * 60); // Chuyển sang giờ

    // Trừ thời gian nghỉ trưa nếu nó nằm trong khoảng thời gian làm việc
    if (start < lunchStart && end > lunchEnd) {
      totalTime -= 1; // Trừ 1 giờ nghỉ trưa
    } else if (start < lunchEnd && end > lunchEnd) {
      totalTime -= (lunchEnd - start) / (1000 * 60 * 60); // Trừ thời gian từ start đến hết nghỉ trưa
    } else if (start < lunchStart && end > lunchStart) {
      totalTime -= (end - lunchStart) / (1000 * 60 * 60); // Trừ thời gian từ bắt đầu nghỉ trưa đến end
    }

    return totalTime;
  } else {
    return 0; // Nếu startTime hoặc endTime rỗng, trả về 0
  }
}
export async function checkTaskIdExists(data) {
  try {
    return await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getItem("Timesheet");
      const table = sheet.tables.getItem("TblTimesheet");
      const rows = table.rows.load("values");

      await context.sync();

      let result = {
        rowIndex: -1,
        startTime: null,
        endTime: null,
      };
      for (let i = 0; i < rows.items.length; i++) {
        const row = rows.items[i].values[0];
        if (excelDateToJSDate(row[0]) === data.date && row[1] === data.taskID && row[2] === data.pic) {
          result.rowIndex = i;
          result.startTime = row[3];
          result.endTime = row[4];
          break;
        }
      }

      return result;
    });
  } catch (error) {
    console.error(error);
    return {
      rowIndex: -1,
      startTime: null,
      endTime: null,
    };
  }
}
